<h1 id="应用管理" tabindex="-1"><a class="header-anchor" href="#应用管理" aria-hidden="true">#</a> 应用管理</h1>
<blockquote>
<p>{warning} 企业微信在17年11月对 API 进行了大量的改动，应用管理部分已经没啥用了</p>
</blockquote>
<p>应用管理是企业微信中比较特别的地方，因为它的使用是不基于应用的，或者说基于任何一个应用都能访问这些 API，所以在用法上是直接调用 work 实例的 <code>agent</code> 属性。</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$config</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
    <span class="token operator">...</span>
<span class="token punctuation">]</span><span class="token punctuation">;</span>

<span class="token variable">$app</span> <span class="token operator">=</span> <span class="token class-name static-context">Factory</span><span class="token operator">::</span><span class="token function">work</span><span class="token punctuation">(</span><span class="token variable">$config</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br></div></div><h2 id="应用列表" tabindex="-1"><a class="header-anchor" href="#应用列表" aria-hidden="true">#</a> 应用列表</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$agents</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token property">agent</span><span class="token operator">-></span><span class="token function">list</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span> <span class="token comment">// 测试拿不到内容</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h2 id="应用详情" tabindex="-1"><a class="header-anchor" href="#应用详情" aria-hidden="true">#</a> 应用详情</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$agents</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token property">agent</span><span class="token operator">-></span><span class="token function">get</span><span class="token punctuation">(</span><span class="token variable">$agentId</span><span class="token punctuation">)</span><span class="token punctuation">;</span> <span class="token comment">// 只能传配置文件中的 id，API 改动所致</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h2 id="设置应用" tabindex="-1"><a class="header-anchor" href="#设置应用" aria-hidden="true">#</a> 设置应用</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$agents</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token property">agent</span><span class="token operator">-></span><span class="token function">set</span><span class="token punctuation">(</span><span class="token variable">$agentId</span><span class="token punctuation">,</span> <span class="token punctuation">[</span><span class="token string single-quoted-string">'foo'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'bar'</span><span class="token punctuation">]</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div>